import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom'; 
import { jwtDecode } from 'jwt-decode';
import styles from './Login.module.css'; 

const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [message, setMessage] = useState('');

  const navigate = useNavigate(); 

  const handleLogin = async () => {
    if (!email || !password) {
      setMessage("Vui lng in y  thng tin!");
      return;
    }

    try {
      const response = await axios.post('http://localhost:5000/api/auth/login', {
        EMAIL: email,
        MATKHAU: password,
      });

    const decoded = jwtDecode(response.data.token);
    console.log(' Token Decoded:', decoded); 
    const userRole = decoded.role;
    const MAKH = decoded.MAKH;         //   SA  y
    const username = decoded.TEN;

    localStorage.setItem('token', response.data.token);
    localStorage.setItem('MAKH', MAKH);         //  By gi s c gi tr ng
    localStorage.setItem('username', username);


      setMessage("ng nhp thnh cng!");

      //  iu hng theo vai tr
      if (userRole === 'admin') {
        navigate('/admin');
      } else {
        navigate('/');
      }

    } catch (err) {
      setMessage(err.response?.data?.message || "ng nhp tht bi!");
      console.error('Login failed:', err);
    }
  };

  return (
    <div className={styles.container}>
      <h1>ng Nhp</h1>
      <div className={styles.formGroup}>
        <input
          type="email"
          placeholder="Email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          className={styles.inputField}
        />
      </div>
      <div className={styles.formGroup}>
        <input
          type="password"
          placeholder="Mt khu"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          className={styles.inputField}
        />
      </div>
      <button onClick={handleLogin} className={styles.button}>ng nhp</button>
      {message && <div className={styles.message}>{message}</div>}

      <div className={styles.registerPrompt} style={{ marginTop: '1rem', textAlign: 'center' }}>
        Cha c ti khon?{' '}
        <span
          style={{ color: 'blue', cursor: 'pointer', textDecoration: 'underline' }}
          onClick={() => navigate('/register')}
        >
          ng k ngay
        </span>
      </div>
    </div>
  );
};

export default Login;
