import React, { useState } from 'react';
import axios from 'axios';
import styles from './Register.module.css'; // Import CSS Module
import { useNavigate } from 'react-router-dom'; // Import useNavigate

const Register = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [name, setName] = useState('');
  const [address, setAddress] = useState('');
  const [phone, setPhone] = useState('');
  const [message, setMessage] = useState('');

  const navigate = useNavigate(); // Khi to useNavigate  iu hng trang

  const handleRegister = async () => {
    if (!name || !email || !password || !address || !phone) {
      setMessage("Vui lng in y  thng tin!");
      return;
    }

    try {
      const response = await axios.post('http://localhost:5000/api/auth/register', {
        EMAIL: email,
        MATKHAU: password,
        TEN: name,
        DCHI: address,
        DTHOAI: phone,
      });

      setMessage("ng k thnh cng!");
      // Sau khi ng k thnh cng, chuyn n trang ng nhp
      setTimeout(() => navigate('/login'), 2000); // 2 giy sau khi thng bo ng k thnh cng, chuyn n trang ng nhp
      console.log(response.data);
    } catch (err) {
      if (err.response && err.response.data) {
        setMessage(err.response.data.message); // Hin th thng bo li t backend
      } else {
        setMessage('ng k tht bi!');
      }
      console.error('Registration failed:', err);
    }
  };

  return (
    <div className={styles.container}>
      <h1>ng K</h1>
      <div className={styles.formGroup}>
        <input
          type="text"
          placeholder="Tn y "
          value={name}
          onChange={(e) => setName(e.target.value)}
          className={styles.inputField} // S dng class t CSS module
        />
      </div>
      <div className={styles.formGroup}>
        <input
          type="email"
          placeholder="Email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          className={styles.inputField} // S dng class t CSS module
        />
      </div>
      <div className={styles.formGroup}>
        <input
          type="password"
          placeholder="Mt khu"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          className={styles.inputField} // S dng class t CSS module
        />
      </div>
      <div className={styles.formGroup}>
        <input
          type="text"
          placeholder="a ch"
          value={address}
          onChange={(e) => setAddress(e.target.value)}
          className={styles.inputField} // S dng class t CSS module
        />
      </div>
      <div className={styles.formGroup}>
        <input
          type="text"
          placeholder="S in thoi"
          value={phone}
          onChange={(e) => setPhone(e.target.value)}
          className={styles.inputField} // S dng class t CSS module
        />
      </div>
      <button onClick={handleRegister} className={styles.button}>ng k</button>
      {message && <div className={`${styles.message} ${message === 'ng k thnh cng!' ? 'success' : 'error'}`}>{message}</div>} {/* Hin th thng bo */}
    </div>
  );
};

export default Register;
