export default function FaqSection() {
  const [activeIndex, setActiveIndex] = useState(null);

  const toggle = (index) => {
    setActiveIndex(activeIndex === index ? null : index);
  };

  return (
    <section className={styles.faqSection}>
      <div className={styles.intro}>
        <h2 className={styles.heading}>Q&amp;A</h2>
        <p className={styles.subtext}>
          Nu y l ln u tin bn bit n S, chc chn bn s c rt nhiu cu hi v dch v,
          thi gian hot ng v cc chnh sch ca chng ti. Hy vng phn Q&amp;A di y s gip bn
          gii p nhng thc mc thng gp nht khi s dng dch v ti S Spa.
        </p>
      </div>

      <div className={styles.image}>
        <img src="/images/faq-hero.jpg" alt="Giao tip khch hng ti l tn" />
      </div>

      <div className={styles.faqBox}>
        <h3 className={styles.faqTitle}>Cu hi thng gp</h3>
        <ul className={styles.accordion}>
          {faqs.map((item, index) => (
            <li key={index} onClick={() => toggle(index)} className={styles.accordionItem}>
              <div className={styles.accordionHeader}>
                <span>{item}</span>
                <span>{activeIndex === index ? "" : "+"}</span>
              </div>
              {activeIndex === index && (
                <div className={styles.accordionContent}>
                  <p>{faqDescriptions[item] || "Ni dung ang cp nht."}</p>
                </div>
              )}
            </li>
          ))}
        </ul>
      </div>
    </section>
  );
}
