import React, { useState, useEffect } from 'react';
import axios from 'axios';
import styles from './ServiceForm.module.css'; // Import CSS module

const EmployeesList = () => {
  const [employees, setEmployees] = useState([]);
  const [formData, setFormData] = useState({
    TEN: '',
    DCHI: '',
    DTHOAI: '',
    CHUYENMON: '',
    DIEMDANHGIA: '',
    HINHANH: '',
  });
  const [editingId, setEditingId] = useState(null);
  const [message, setMessage] = useState('');

  // Ly danh sch nhn vin t API
  useEffect(() => {
    fetchEmployees();
  }, []);

  const fetchEmployees = async () => {
    try {
      const response = await axios.get('http://localhost:5000/api/employees');
      setEmployees(response.data);
    } catch (error) {
      console.error('Li khi ly danh sch nhn vin:', error);
    }
  };

  // X l thm hoc cp nht nhn vin
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (editingId) {
        // Cp nht nhn vin
        await axios.put(`http://localhost:5000/api/employees/${editingId}`, formData);
        setMessage('Cp nht nhn vin thnh cng!');
      } else {
        // Thm mi nhn vin
        await axios.post('http://localhost:5000/api/employees', formData);
        setMessage('Thm nhn vin thnh cng!');
      }
      fetchEmployees();
      setFormData({
        TEN: '',
        DCHI: '',
        DTHOAI: '',
        CHUYENMON: '',
        DIEMDANHGIA: '',
        HINHANH: '',
      });
      setEditingId(null);
    } catch (error) {
      console.error('Li khi lu nhn vin:', error);
      setMessage('Li khi lu nhn vin!');
    }
  };

  // X l xa nhn vin
  const handleDelete = async (id) => {
    try {
      await axios.delete(`http://localhost:5000/api/employees/${id}`);
      fetchEmployees();
      setMessage('Xa nhn vin thnh cng!');
    } catch (error) {
      console.error('Li khi xa nhn vin:', error);
      setMessage('Li khi xa nhn vin!');
    }
  };

  // X l chnh sa nhn vin
  const handleEdit = (employee) => {
    setFormData(employee);
    setEditingId(employee.MANV);
  };

  return (
    <div className={styles['form-container']}>
      <h1>Qun L Nhn Vin</h1>

      {/* Form thm/sa nhn vin */}
      <form onSubmit={handleSubmit}>
        <div className={styles['form-group']}>
          <label>Tn Nhn Vin</label>
          <input
            type="text"
            value={formData.TEN}
            onChange={(e) => setFormData({ ...formData, TEN: e.target.value })}
            required
          />
        </div>
        <div className={styles['form-group']}>
          <label>a Ch</label>
          <input
            type="text"
            value={formData.DCHI}
            onChange={(e) => setFormData({ ...formData, DCHI: e.target.value })}
            required
          />
        </div>
        <div className={styles['form-group']}>
          <label>S in Thoi</label>
          <input
            type="text"
            value={formData.DTHOAI}
            onChange={(e) => setFormData({ ...formData, DTHOAI: e.target.value })}
            required
          />
        </div>
        <div className={styles['form-group']}>
          <label>Chuyn Mn</label>
          <input
            type="text"
            value={formData.CHUYENMON}
            onChange={(e) => setFormData({ ...formData, CHUYENMON: e.target.value })}
            required
          />
        </div>
        <div className={styles['form-group']}>
          <label>im nh Gi</label>
          <input
            type="number"
            value={formData.DIEMDANHGIA}
            onChange={(e) => setFormData({ ...formData, DIEMDANHGIA: e.target.value })}
            required
          />
        </div>
        <div className={styles['form-group']}>
          <label>Hnh nh</label>
          <input
            type="text"
            value={formData.HINHANH}
            onChange={(e) => setFormData({ ...formData, HINHANH: e.target.value })}
          />
        </div>
        <button type="submit">{editingId ? 'Cp Nht' : 'Thm Mi'}</button>
      </form>
      {message && <p className={styles.message}>{message}</p>}

      {/* Danh sch nhn vin */}
      <table className="table table-bordered">
        <thead>
          <tr>
            <th>#</th>
            <th>Tn</th>
            <th>a Ch</th>
            <th>S in Thoi</th>
            <th>Chuyn Mn</th>
            <th>im nh Gi</th>
            <th>Hnh nh</th>
            <th>Hnh ng</th>
          </tr>
        </thead>
        <tbody>
          {employees.map((employee, index) => (
            <tr key={employee.MANV}>
              <td>{index + 1}</td>
              <td>{employee.TEN}</td>
              <td>{employee.DCHI}</td>
              <td>{employee.DTHOAI}</td>
              <td>{employee.CHUYENMON}</td>
              <td>{employee.DIEMDANHGIA}</td>
              <td>
                <img src={employee.HINHANH} alt="Hnh nh" width="50" />
              </td>
              <td>
                <button onClick={() => handleEdit(employee)}>Sa</button>
                <button onClick={() => handleDelete(employee.MANV)}>Xa</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default EmployeesList;