export default function ServiceDetail() {
  const { id } = useParams();
  const service = serviceData[id];

  if (!service) return <div className={styles.notFound}>Dch v khng tn ti.</div>;

  return (
    <div className={styles.detailContainer}>
      <div className={styles.card}>
        <img src={service.image} alt={service.title} className={styles.image} />
        <div className={styles.info}>
          <h1 className={styles.title}>{service.title}</h1>
          <div className={styles.price}>{service.price}</div>
          <p className={styles.desc}>{service.desc}</p>
          <div className={styles.detail}>{service.detail}</div>
          <Link to="/booking" className={styles.bookBtn}>t lch ngay</Link>
        </div>
      </div>
      <Link to="/" className={styles.backLink}> Quay v trang ch</Link>
    </div>
  );
}