import React, { useEffect, useState } from 'react';
import styles from './Profile.module.css';
import { useNavigate } from 'react-router-dom';

const Profile = () => {
  const [user, setUser] = useState({
    TEN: '',
    DCHI: '',
    DTHOAI: '',
    EMAIL: '',
  });

  const [isEditing, setIsEditing] = useState(false); //  NEW
  const [message, setMessage] = useState('');
  const [loading, setLoading] = useState(true);

  const navigate = useNavigate();

  const MAKH = localStorage.getItem('MAKH');
  const role = localStorage.getItem('role');

  useEffect(() => {
    if (role === 'admin') {
      navigate('/admin');
      return;
    }

    if (!MAKH) {
      console.warn('Khng tm thy MAKH, chuyn hng v trang ng nhp');
      navigate('/login');
      return;
    }

    const fetchUser = async () => {
      try {
        const res = await fetch(`http://localhost:5000/api/khachhang/${MAKH}`);
        const data = await res.json();
        setUser(data);
      } catch (error) {
        console.error('Li khi ly thng tin:', error);
        setMessage('Khng th ti thng tin khch hng.');
      } finally {
        setLoading(false);
      }
    };

    fetchUser();
  }, [MAKH, role, navigate]);

  const handleChange = (e) => {
    setUser({ ...user, [e.target.name]: e.target.value });
  };

  const handleSave = async () => {
    try {
      const res = await fetch(`http://localhost:5000/api/khachhang/${MAKH}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(user),
      });

      if (res.ok) {
        setMessage(' Cp nht thnh cng!');
        setIsEditing(false); //  quay li xem
      } else {
        setMessage(' Cp nht tht bi!');
      }
    } catch (error) {
      console.error('Li khi cp nht:', error);
      setMessage(' Khng th cp nht d liu.');
    }
  };

  if (loading) return <p>ang ti thng tin khch hng...</p>;

  return (
    <div className={styles.profileContainer}>
      <h2>Thng tin c nhn</h2>

      <label>H tn</label>
      {isEditing ? (
        <input name="TEN" value={user.TEN} onChange={handleChange} />
      ) : (
        <div>{user.TEN}</div>
      )}

      <label>a ch</label>
      {isEditing ? (
        <input name="DCHI" value={user.DCHI} onChange={handleChange} />
      ) : (
        <div>{user.DCHI}</div>
      )}

      <label>S in thoi</label>
      {isEditing ? (
        <input name="DTHOAI" value={user.DTHOAI} onChange={handleChange} />
      ) : (
        <div>{user.DTHOAI}</div>
      )}

      <label>Email</label>
      {isEditing ? (
        <input name="EMAIL" value={user.EMAIL} onChange={handleChange} />
      ) : (
        <div>{user.EMAIL}</div>
      )}

      {isEditing ? (
        <>
          <button onClick={handleSave}>Lu thay i</button>
          <button onClick={() => setIsEditing(false)} style={{ marginLeft: '10px' }}>
            Hy
          </button>
        </>
      ) : (
        <button onClick={() => setIsEditing(true)}>Sa thng tin</button>
      )}

      {message && (
        <p style={{ marginTop: '10px', color: message.startsWith('') ? 'green' : 'red' }}>
          {message}
        </p>
      )}
    </div>
  );
};

export default Profile;
